.App {
    font-family: sans-serif;
    text-align: center;
  }
  
.editable-div {
  width: 100%;
  min-height: 200px;
  border: 1px solid #ccc;
  padding: 10px;
  font-family: Arial, sans-serif;
  font-size: 16px;
  box-sizing: border-box;
}

.btn-wrix-sticky{
  margin: 10px;
  height: 35px;
  width: 35px;
  background-color: rgba(0, 0, 0, 0);
  border: none;
}

.btn-wrix-sticky:hover{
  background-color: #ccc;
  border: none;
  border-radius: 5px;
}

.div-wrix-sticky{
  border: 1px solid #ccc;
  position: relative;
  border-top-left-radius: 5px;
  border-top-right-radius: 5px;
  display: flex;
  flex-direction: row;
}